<template>
  <div class="Landing" :class="['slide-' + animationDetails.currentStep, { 
      'loaded': animationDetails.isLoaded, 
      'scrolled' : animationDetails.isScrolled, 
      'scrolling' : animationDetails.isScrolling }]" ref="landing">
    <div class="background" :class="{ isOff: !animationDetails.active}">
      {{ loaded }}
      <Three :isStarted="loaded" :isActive="animationDetails.active" :step="animationDetails.renderStep" @loaded="onLoaded" @progress="onProgress"></Three>
    </div>
    <div class="background-map"></div>

    <section id="slide-0" :class="[{ isActive: animationDetails.currentStep === 0, loaded: loaded }]" data-image-left="/static/wpt__half.jpg"
      data-image-right="/static/wroclavia__half.jpg" data-image-center="/static/wpt__small.jpg">
    </section>

    <section id="slide-1">
      <h1>Welcome to PGS</h1>
      <h2>lorem ipsum dolor sit amet</h2>
    </section>

    <section id="slide-2">
      <h1>hello world - slide 2</h1>
    </section>

    <section id="slide-3">
    </section>

    <section id="slide-4">
      <h1>hello world - slide 4</h1>
    </section>

    <section id="slide-5">
      <h1>hello world - slide 5</h1>
    </section>

    <div class="mouse">
      <div class="scroll-wheel"></div>
    </div>

    <div class="scroll">
      <p>keep scrolling...</p>
    </div>

    <div class="loader">
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
      <p>{{ progress }} %</p>
    </div>

    <div class="Menu" :class="{ isActive: animationDetails.currentStep > 0 }">
      <a class="logo"><span>PGS Software</span></a>
      <ul>
        <li>About</li>
        <li>Contact</li>
      </ul>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import {
    throttle,
    isFunction
  } from 'lodash';

  import Three from './Three';

  const scrollSettings = {
    sensivity: 30,
    duration: 600
  };

  let ticking = false;
  let container;
  let touchY = 0;

  let animationDetails = {
    active: false,
    animatedSteps: [1, 2, 3],
    animationMap: {
      1: 0,
      2: 1,
      3: 2
    },
    currentStep: 0,
    isLoaded: false,
    isScrolled: false,
    isScrolling: false,
    renderStep: 0,
    totalSlides: 6,
  };

  export default {
    name: 'landing',
    components: {
      Three,
    },
    data() {
      return {
        animationDetails,
        progress: 0,
        loaded: false,
        imageLoaded: false,
      };
    },
    methods: {
      onLoaded() {
        animationDetails.isLoaded = true;
      },
      onProgress(p) {
        this.progress = Math.ceil(p * 100);
      }
    },
    mounted() {
      window.addEventListener('wheel', throttle(fullPageScroll, 60), false);
      window.addEventListener('touchstart', throttle(onTouchStart, 60), false);
      window.addEventListener('touchend', throttle(onTouchEnd, 60), false);

      const section = document.getElementById('slide-0');
      const images = {
        left: section.dataset.imageLeft,
        right: section.dataset.imageRight,
        center: section.dataset.imageCenter,
      };

      console.log(section.dataset);

      let imagesLoaded = 0;

      const imageSmall = new Image();
      imageSmall.src = images.center;
      imageSmall.setAttribute(this.$options._scopeId, '');
      imageSmall.classList.add('center');

      const imageLeft = new Image();
      imageLeft.setAttribute(this.$options._scopeId, '');
      imageLeft.classList.add('left');

      const imageRight = new Image();
      imageRight.setAttribute(this.$options._scopeId, '');
      imageRight.classList.add('right');

      const onImageLoadComplete = () => {
        this.loaded = true;
      }

      const onImageLoad = () => {
        console.log('onImageLoad');
          imagesLoaded++;
          if (imagesLoaded === 2) {
            onImageLoadComplete();
          }
      }


      imageSmall.onload = () => {
        console.log('imageSmall!')
        imageLeft.onload = onImageLoad;
        imageRight.onload = onImageLoad;

        imageLeft.src = images.left;
        imageRight.src = images.right;

        
        section.appendChild(imageLeft);
        section.appendChild(imageRight);
      };
      section.appendChild(imageSmall);
    },
  };

  function onTouchStart(e) {
    touchY = e.touches[0].clientY;
  };

  function onTouchEnd(e) {
    const delta = touchY - e.changedTouches[0].clientY;
    fullPageScroll({
      wheelDelta: -delta
    });
  };

  function fullPageScroll(e) {
    const delta = e.wheelDelta;

    if (ticking != true && animationDetails.isLoaded) {
      if (delta <= -scrollSettings.sensivity) { //nextItem
        //Down scroll
        ticking = true;
        if (animationDetails.currentStep !== animationDetails.totalSlides - 1) {
          animationDetails.currentStep++;
        }
        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }

      if (delta >= scrollSettings.sensivity) { //prevItem
        //Up scroll
        ticking = true;
        if (animationDetails.currentStep !== 0) {
          animationDetails.currentStep--;
        }

        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }
    }
  }

  function slideDurationTimeout(slideDuration) {
    setTimeout(function () {
      ticking = false;
    }, slideDuration);
  }

  function mapSlideStepToRender() {

  }

  function scrollItems() {
    animationDetails.isScrolling = true;
    setTimeout(() => {
      animationDetails.isScrolling = false; // @todo: add checking if on last step
    }, 3500);

    animationDetails.active = animationDetails.animatedSteps.reduce((prev, n) => {
      return animationDetails.currentStep === n || prev;
    }, false);

    const mappedStep = animationDetails.animationMap[animationDetails.currentStep];

    if (mappedStep !== undefined) {
      animationDetails.renderStep = mappedStep;
    }

    if (!animationDetails.isScrolled) {
      animationDetails.isScrolled = true;
    }
  }

  // --- class helpers
  function hasClass(el, className) {
    if (el.classList) {
      return el.classList.contains(className);
    } else {
      return !!el.className.match(new RegExp('(\\s|^)' + className + '(\\s|$)'))
    }
  }

  function addClass(el, className) {
    if (el.classList) {
      el.classList.add(className);
    } else if (!hasClass(el, className)) {
      el.className += " " + className;
    }
  }

  function removeClass(el, className) {
    if (el.classList) {
      el.classList.remove(className);
    } else if (hasClass(el, className)) {
      var reg = new RegExp('(\\s|^)' + className + '(\\s|$)');
      el.className = el.className.replace(reg, ' ');
    }
  }

</script>

<style lang="scss" src="./Landing.scss" scoped></style>



