import Vue from 'vue';
import Router from 'vue-router';
import Landing from '@/components/Landing';
import Three from '@/components/Three';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing,
    },
    {
      path: '/debug',
      name: 'Three',
      component: Three,
      props: {
        isActive: true,
        isDebug: true,
        step: 2,
      },
    },
  ],
});



